	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Lowercase Reform","date":"17 Aug 2004","slug":"/2004-08-17-lowercase-reform/","tags":"Y2004"},"html":"<p><a href=\"http://kalsey.com/2004/08/lowercase_style/\" title=\"Lowercase style :: Kalsey Consulting Group\">Adam Kalsey</a> points to a change in <a href=\"http://www.wired.com/news/culture/0,1284,64596,00.html\" title=\"It's Just the 'internet' Now\">Wired Style</a> that internet and web are now written in lower case. Note that this <a href=\"http://www.papascott.de/archives/2004/08/07/good-speling/\">spelling reform</a> has nothing to do with politics, but what (now) makes the most sense. The <a href=\"http://www.economist.com/research/StyleGuide/\">Economist Style Guide</a> is also a good source of good sense</p>"},"next":{"fields":{"title":"Should I Stay Or Should I Go?","date":"16 Aug 2004","slug":"/2004-08-16-should-i-stay-or-should-i-go/"}},"previous":{"fields":{"title":"Light. End. Tunnel.","date":"18 Aug 2004","slug":"/2004-08-18-light-end-tunnel/"}}},{"node":{"fields":{"title":"Should I Stay Or Should I Go?","date":"16 Aug 2004","slug":"/2004-08-16-should-i-stay-or-should-i-go/","tags":"Y2004"},"html":"<p>At work we're a waiting on the result of a court order <em><a href=\"http://dict.leo.org/?search=injunction\">(einstweilige Verfügung)</a></em>. I thought there was a song about waiting on an injunction, but I guess I was thinking of Losing My Religion by R.E.M. So we'll have to go with <a href=\"http://www.geocities.com/regitorico/shouldi.htm\">The Clash</a>: </p>\n<p>\"Should I stay or should I go now?<br />\nShould I stay or should I go now?<br />\nIf I go there will be trouble<br />\nAn' if I stay it will be double<br />\nSo you gotta let me know<br />\nShould I stay or should I go?\"</p>"},"next":{"fields":{"title":"Naturalized","date":"15 Aug 2004","slug":"/2004-08-15-naturalized/"}},"previous":{"fields":{"title":"Lowercase Reform","date":"17 Aug 2004","slug":"/2004-08-17-lowercase-reform/"}}},{"node":{"fields":{"title":"Naturalized","date":"15 Aug 2004","slug":"/2004-08-15-naturalized/","tags":"Y2004"},"html":"<p>A Russian immigrant, <a href=\"http://www.spiegel.de/sport/sonst/0,1518,313333,00.html\" title=\"Olympia: Kampfsportlerin holt erste deutsche Medaille - SPIEGEL ONLINE\">Julia Matijass</a>, won the first medal for Germany in Athens.</p>"},"next":{"fields":{"title":"Pinellas County evacuation","date":"13 Aug 2004","slug":"/2004-08-13-pinellas-county-evacuation/"}},"previous":{"fields":{"title":"Should I Stay Or Should I Go?","date":"16 Aug 2004","slug":"/2004-08-16-should-i-stay-or-should-i-go/"}}}],"pathPrefix":"","first":false,"last":false,"index":433,"pageCount":1101}}

